// DecompressTextForm.js
import React, { useState } from 'react';
import { Box, TextField, Button, Typography, Alert } from '@mui/material';
import { useNavigate } from 'react-router-dom';  // Import useNavigate for navigation
import pako from 'pako';

function DecompressTextForm() {
  const [text, setText] = useState('');
  const [error, setError] = useState(false); // 
  const navigate = useNavigate();  // hook to handle navigation

  // Base64
  const decompressText = (base64String) => {
    try {
      const compressedBytes = Uint8Array.from(atob(base64String), c => c.charCodeAt(0));
      const utf8Bytes = pako.ungzip(compressedBytes);
      const decodedText = new TextDecoder().decode(utf8Bytes);
      setError(false); // 
      return decodedText;
    } catch (error) {
      setError(true); // 
      return null;
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const decompressedText = decompressText(text);

    if (decompressedText) {
      try {
        const travelData = JSON.parse(decompressedText); // Convert the decompressed string to JSON

        // Navigate to the /travel route and pass the travelData as state
        navigate("/tabi-jaws/travel", { state: travelData });

      } catch (error) {
        setError(true); // Set error if JSON parsing fails
      }
    }
  };

  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{
        maxWidth: 600, // 
        width: '100%', // 100%
        mx: 'auto',
        p: 4,
        borderRadius: 3,
        boxShadow: '0 4px 20px rgba(0, 0, 0, 0.2)',
        background: 'linear-gradient(135deg, #00c6ff 0%, #0072ff 100%)',
        color: '#fff',
        textAlign: 'center',
        overflow: 'hidden',
      }}
    >
      <Typography variant="h4" fontWeight="bold" sx={{ mb: 3, color: '#fff', fontFamily: 'Poppins, sans-serif' }}>
        
      </Typography>

      {/* Alert */}
      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          
        </Alert>
      )}

      <TextField
        label=""
        variant="filled"
        fullWidth
        value={text}
        onChange={(e) => setText(e.target.value)}
        multiline
        minRows={5}
        sx={{
          mb: 4,
          backgroundColor: '#ffffff90',
          borderRadius: 2,
          '& .MuiFilledInput-root': {
            backgroundColor: 'transparent',
          },
          '& .MuiFilledInput-underline:before': {
            borderBottomColor: '#ffffff66',
          },
          '& .MuiFilledInput-underline:hover:before': {
            borderBottomColor: '#ffffff',
          },
          '& .MuiInputLabel-root': {
            color: '#ffffffa0',
          },
          '& .MuiInputLabel-root.Mui-focused': {
            color: '#ffffff',
          },
        }}
      />
      <Button
        type="submit"
        variant="contained"
        fullWidth
        sx={{
          mt: 2,
          backgroundColor: '#0072ff',
          ':hover': {
            backgroundColor: '#005bb5',
          },
        }}
      >
        
      </Button>
    </Box>
  );
}

export default DecompressTextForm;
