// DataImport.js
import React from "react";
import DecompressTextForm from "../component/DecompressTextForm";
import { Box } from "@mui/material";
import Header from "../component/Header"; // 
import { useNavigate } from "react-router-dom"; // React RouteruseNavigate

function DataImport() {
  const pageTitle = "";

  // react-router-domuseNavigate
  const navigate = useNavigate();

  // 
  const handleBackClick = () => {
    // React Router'/'
    navigate("/tabi-jaws");
  };

  return (
    <Box
      sx={{
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        background: "linear-gradient(120deg, #f6d365 0%, #fda085 100%)",
        p: 0, // 
      }}
    >
      <Header pageTitle={pageTitle} onBackClick={handleBackClick} />
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          flex: 1, // 
          p: 2,
        }}
      >
        <DecompressTextForm />
      </Box>
    </Box>
  );
}

export default DataImport;
