// Home.js
import React, { useEffect } from "react";
import TravelForm from "../component/TravelForm";
import { Box, Typography, useMediaQuery, Link } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import { useNavigate, useLocation } from "react-router-dom"; // useNavigate, useLocation
import pako from "pako"; // pako

function Home() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md")); // md
  const navigate = useNavigate();
  const location = useLocation(); // 

  // Base64
  const decompressText = (base64String) => {
    try {
      const compressedBytes = Uint8Array.from(atob(base64String), (c) =>
        c.charCodeAt(0)
      );
      const utf8Bytes = pako.ungzip(compressedBytes);
      const decodedText = new TextDecoder().decode(utf8Bytes);
      // JSON
      const parsedData = JSON.parse(decodedText);
      return parsedData; // JSON
    } catch (error) {
      return null; // null
    }
  };

  useEffect(() => {
    //  travelData 
    const params = new URLSearchParams(location.search);
    const travelDataBase64 = params.get("travelData");

    if (travelDataBase64) {
      const decompressedData = decompressText(travelDataBase64);
      if (decompressedData) {
        navigate("/tabi-jaws/preview", { state: decompressedData }); // travelDatastate
      } else {
        // 
        navigate("/tabi-jaws"); // 
      }
    }
  }, [location.search, navigate]);

  return (
    <Box
      sx={{
        minHeight: "100vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        background: "linear-gradient(120deg, #f6d365 0%, #fda085 100%)",
        p: 2,
        flexDirection: isMobile ? "column" : "row", // 
      }}
    >
      <Box
        sx={{
          textAlign: "center",
          color: "#fff",
          mb: isMobile ? 2 : 4, // 
        }}
      >
        <Typography variant="h3" fontWeight="bold" sx={{ mb: 1 }}>
          
        </Typography>
        <Typography variant="h5" sx={{ opacity: 0.8, mb: 2 }}>
          
        </Typography>
        
        <Link
          href="/tabi-jaws/data"
          underline="hover"
          sx={{ color: "#fff", fontWeight: "bold" }}
        >
          
        </Link>
      </Box>
      <TravelForm />
    </Box>
  );
}

export default Home;
